.mainBlock {
  justify-content: center;
  display: flex;
  max-width: 480px;
  height: 550px;
}

.box {
  flex-direction: column;
  font-family: 'Montserrat', sans-serif;
  justify-content: space-evenly;
  height: 500px;
}

.secondBox {
  display: flex;
  flex-direction: column;
  height: 100%;
  align-items: center;
  justify-content: space-evenly;
}

.thirdBox {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 120px;
}

.text {
  font-size: 15px;
  font-weight: 400;
  line-height: 24px;
  text-align: center;
  color: #000000;
}

.secondText {
  font-size: 15px;
  font-weight: 400;
  line-height: 24px;
  text-align: center;
  color: #000000;
}
