.main {
  width: 100%;
  justify-content: center;
  display: flex;
}

.signUpBlock {
  flex-direction: column;
  height: 500px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
}

.box {
  height: 500px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

.input {
  width: 382px;
  height: 28px;
}

.text {
  font-size: 15px;
  font-weight: 400;
  line-height: 24px;
  text-align: center;
  color: #000000;
}

.error {
  color: darkred;
  text-align: left;
  font-size: 13px;
  padding-top: 5px;
}

.button {
  width: 347px;
  height: 36px;
  margin-top: 10px;
  color: #ffffff;
  border-radius: 30px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  font-size: 16px;
  border-color: inherit;
  background: #366eff;
  box-shadow: 0 4px 18px rgba(54, 110, 255, 0.35), inset 0 1px 0 rgba(255, 255, 255, 0.3);
}

.button:hover {
  background: rgba(0, 102, 204, 0.53);
  color: #ffffff;
}
